import { useState } from "react";
import { Link } from "react-router-dom";
import { Button, Input } from "../../components";
import api from "../../Services/api";
import { ButtonDiv, InsideForm, PanelStyled } from "./ChangePasswordStyled";

const ChangePassword = () => {
  const [message, setMessage] = useState("");
  const [success, setSuccess] = useState(false);
  const [form, setForm] = useState<{
    senha: string;
    codigoAcesso: string;
  }>({
    codigoAcesso: "",
    senha: "",
  });

  const changePassword = (e: any) => {
    e.preventDefault();
    api
      .patch("auth/recuperar-senha", form)
      .then((res) => console.log(res))
      .catch((error) => console.log(error));
  };

  const changeSuccess = () => {
    setSuccess(true);
    setMessage("Senha alterada. Faa login!");
  };

  return (
    <PanelStyled>
      <h4>Altere sua senha!</h4>
      <InsideForm>
        <Input
          height="40px"
          onChange={(val: string) => setForm({ ...form, codigoAcesso: val })}
          placeholder="Cdigo"
          width="450px"
        />
        <Input
          height="40px"
          onChange={(val: string) => setForm({ ...form, senha: val })}
          placeholder="Nova senha"
          type="password"
          width="450px"
        />
        <Input
          height="40px"
          onChange={() => console.log("onChange")}
          placeholder="Confirmar nova senha"
          type="password"
          width="450px"
        />
        <ButtonDiv>
          <Link to="/requestcode">
            No possui um cdigo? Solicite um agora!
          </Link>
          <Button
            onClick={(e: any) => changePassword(e)}
            text={"Alterar"}
            disabled={false}
          />
        </ButtonDiv>
        {success === true ? (
          <Link to="/" style={{ color: "green" }}>
            {message}
          </Link>
        ) : (
          <></>
        )}
      </InsideForm>
    </PanelStyled>
  );
};
export default ChangePassword;
